body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  white-space: nowrap;
  background-color: #181818;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sections{
  height: 200px;
}
.title {
  height: 30vh;
  right: -1%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: yellow;
  font-size: 3.5em;
  white-space: pre;
}
.about{
  height: 100vh;
}
.contact{
  height: 100vh;
}

.sectiontext{
  display: flex;
  justify-content: center;
  align-items: center;
  color: yellow;
  font-size: 1.5em;
  white-space: pre;
}

.section {
  color: yellow;
  cursor: pointer;  
  letter-spacing: 0.10em;
  font-size: 1.5em;
  padding: 5px 5px;
  position: relative;
}
.section:after {  
  bottom: 0;
  content: "";
  height: 2px;
  left: 50%;
  position: absolute;
  background: #7215BD;
  transition: width 0.4s, left 0.4s;
  width: 0;
}
.section:hover:after { 
  width: 100%; 
  left: 0; 
}

.weight-form{
  margin: auto;
  width: 10%;
  color: white;
}

.output-list{
  color : #7215BD;
  list-style-type: none;
  
}

.label-wrapper{
  text-align: center;
}

.label-output{
  text-align: center;
  height: 30vh;

}

.button{
  display: block;
  width: 104%;
  height: 40px;
  background-color: #303030;
  transition: 0.2s all;
  color: white;
  font-weight: bold;
  font-size: 1.25em;
  padding: 0;
  border: none;
  
}

.button:hover{
  background-color: #585858;
}

.input {
  width: 100%;
  height: 40px;
  background-color: #303030;
  transition: 0.2s all;
  color: white;
  font-weight: bold;
  font-size: 1.25em;
  
}
.input:hover {
  background-color: #585858;
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
}

.output{
  height: 50px;
}

.workout-form{
  height:100vh;
}